<template>
  <div>
    <!-- dropdown of users  -->
    <users-select @item-selected="(val) => this.filterByUsername(val)" />

    <b-card no-body class="col-12">
      <b-row class="mt-3">
        <b-col md="2" class="my-1">
          <b-form-group horizontal label="Per page" class="mb-0">
            <b-form-select
              :options="pageOptions"
              v-model="perPage"
              @change="(val) => setPerPage(val)"
            />
          </b-form-group>
        </b-col>

        <b-col md="2" class="my-1">
          <b-form-group horizontal label="Subscription type" class="mb-0">
            <b-form-select
              :options="suscriptionTypeOptions"
              v-model="suscriptionType"
            />
          </b-form-group>
        </b-col>

        <b-col md="2" class="my-1">
          <b-form-group horizontal label="Subscription Status" class="mb-0">
            <b-form-select
              :options="subscriptionStatusOptions"
              v-model="subscriptionStatus"
            />
          </b-form-group>
        </b-col>

        <b-col md="2" class="my-1" sm="4">
          <b-form-group horizontal label="Sort" class="mb-0">
            <b-input-group>
              <b-form-select v-model="sortBy" :options="sortOptions">
                <option slot="first" :value="null">-- none --</option>
              </b-form-select>

              <b-form-select
                :disabled="!sortBy"
                v-model="sortDesc"
                slot="append"
              >
                <option :value="false">Asc</option>
                <option :value="true">Desc</option>
              </b-form-select>
            </b-input-group>
          </b-form-group>
        </b-col>

        <b-col md="2" class="my-1 d-flex align-items-end" sm="4">
          <b-input-group>
            <router-link :to="'new'" class="btn btn-warning btn-block">
              New Subscription
              <i class="fa fa-plus" />
            </router-link>
          </b-input-group>
        </b-col>

        <b-col md="2" class="my-1 d-flex align-items-end" sm="4">
          <button class="btn btn-success btn-block" @click="csvExport(items)">
            Export CSV
          </button>
        </b-col>
      </b-row>

      <b-table
        :busy="loading"
        :hover="hover"
        :striped="striped"
        :bordered="bordered"
        :small="small"
        :fixed="fixed"
        :items="items"
        :fields="fields"
        :per-page="perPage"
        :sort-by.sync="sortBy"
        :sort-desc.sync="sortDesc"
        class="mt-3"
        responsive="sm"
        id="myTable"
      >
        <template v-slot:cell(username)="data">
          <span class="badge badge-primary" v-if="data.item.user">
            <strong>{{
              data.item.user
                ? data.item.user.username
                : data.item.temp_user.username
            }}</strong>
          </span>
          <span class="badge badge-primary" v-if="data.item.temp_user"
            >{{
              data.item.user
                ? data.item.user.username
                : data.item.temp_user.username
            }}
            - Temporary User</span
          >
        </template>

        <template v-slot:cell(price)="data">
          <span
            >{{ data.item.price }}
            {{ data.item.currency ? data.item.currency.name : 0 }}</span
          >
        </template>

        <template v-slot:cell(subscription_status)="data">
          <span
            class="badge"
            :class="[
              { 'badge-success': data.item.subscription_status === 'active' },
              { 'badge-danger': data.item.subscription_status === 'expired' },
              {
                'badge-warning': data.item.subscription_status === 'cancelled'
              }
            ]"
            >{{ data.item.subscription_status }}</span
          >
        </template>

        <template v-slot:cell(subscription_type)="data">
          <span
            v-if="data.item.subscription_type"
            class="badge badge-warning"
            >{{ data.item.subscription_type }}</span
          >
          <span v-else>-</span>
        </template>

        <template v-slot:cell(status)="data">
          <span
            v-if="data.item.invoice_status === 'paid'"
            class="badge badge-success"
            >{{ data.item.invoice_status }}</span
          >
          <span
            v-if="
              data.item.invoice_status === 'pending' &&
                data.item.plan.renovation
            "
            class="badge badge-warning"
            >Pending Renovation</span
          >
          <span
            v-if="
              data.item.invoice_status === 'pending' &&
                !data.item.plan.renovation
            "
            class="badge badge-warning"
            >{{ data.item.invoice_status }}</span
          >
        </template>

        <template v-slot:cell(value)="data">
          <b>${{ data.item.plan && data.item.plan.price }} USD</b>
        </template>

        <template v-slot:cell(action)="data">
          <router-link
            :to="'edit/' + data.item.id"
            class="btn btn-sm btn-dark ml-1"
          >
            Edit
            <i class="fa fa-pencil" />
          </router-link>
        </template>
      </b-table>

      <nav>
        <paginate
          :page-count="pageCount"
          :page-range="3"
          :click-handler="nextPageCall"
          :prev-text="'Prev'"
          :next-text="'Next'"
          :container-class="'pagination b-pagination pagination-md'"
          :prev-class="'page-item'"
          :prev-link-class="'page-link'"
          :next-class="'page-item'"
          :next-link-class="'page-link'"
          :page-class="'page-item'"
          :page-link-class="'page-link'"
        ></paginate>
      </nav>
    </b-card>
  </div>
</template>

<script>
import UsersSelect from "@/components/UsersSelect";

import _ from "lodash";
import Paginate from "vuejs-paginate";
import vSelect from "@/../node_modules/vue-select/src/components/Select";
import * as table2csv from "table2csv";
import { mapGetters, mapActions } from "vuex";

export default {
  name: "UsersList",
  props: {
    server: { type: String },
    hover: {
      type: Boolean,
      default: true
    },
    striped: {
      type: Boolean,
      default: true
    },
    bordered: {
      type: Boolean,
      default: true
    },
    small: {
      type: Boolean,
      default: true
    },
    fixed: {
      type: Boolean,
      default: false
    }
  },
  components: {
    paginate: Paginate,
    "v-select": vSelect,
    UsersSelect
  },
  data() {
    return {
      fields: [
        { key: "id", sortable: true, label: "ID" },
        { key: "username" },
        { key: "plan_name" },
        { key: "value", label: "Total" },
        { key: "subscription_status", label: "Status" },
        { key: "expiration", label: "Expiration", sortable: true },
        { key: "subscription_type", label: "Subscription Type" },
        {
          key: "created_at",
          label: "Created",
          sortable: true,
          thClass: "text-right",
          tdClass: "text-right"
        },
        {
          key: "action",
          label: "Action",
          thClass: "text-right",
          tdClass: "text-right"
        }
      ],
      pageOptions: [
        5,
        10,
        15,
        20,
        25,
        30,
        40,
        50,
        100,
        250,
        500,
        1000,
        1500,
        2000,
        2500,
        3000,
        3500,
        4000,
        4500,
        5000
      ],
      suscriptionTypeOptions: [
        "bots",
        "bot_premium",
        "renovation",
        "distributor_license"
      ],
      subscriptionStatusOptions: ["active", "expired", "cancelled"],
      sortBy: "status",
      sortDesc: false,
      totalItems: this.listSubscriptions.length,
      perPage: 25,
      suscriptionType: "bots",
      subscriptionStatus: "active",
      pageNum: 1
    };
  },
  computed: {
    sortOptions() {
      return this.fields
        .filter((f) => f.sortable)
        .map((f) => {
          return { text: f.label, value: f.key };
        });
    },
    ...mapGetters("subscriptions", {
      items: "getSubscriptionsList",
      pageCount: "getPageCount",
      loading: "isLoading"
    })
  },
  watch: {
    subscriptionStatus(newVal) {
      this.listSubscriptions({
        subscriptionStatus: newVal,
        suscriptionType: this.suscriptionType,
        perPage: this.perPage,
        pageNum: 1
      });
    },
    suscriptionType(newVal) {
      this.listSubscriptions({
        subscriptionStatus: this.subscriptionStatus,
        suscriptionType: newVal,
        perPage: this.perPage,
        pageNum: 1
      });
    }
  },
  created() {
    this.listSubscriptions({
      subscriptionStatus: this.subscriptionStatus,
      suscriptionType: this.suscriptionType,
      perPage: this.perPage,
      pageNum: this.pageNum
    });
  },
  methods: {
    ...mapActions("subscriptions", {
      listSubscriptions: "fetchList"
    }),
    filterByUsername(username) {
      this.listSubscriptions({
        subscriptionStatus: this.subscriptionStatus,
        suscriptionType: this.suscriptionType,
        perPage: this.perPage,
        pageNum: 1,
        username: username
      });
    },
    setPerPage(val) {
      this.perPage = val;
      this.listSubscriptions({
        subscriptionStatus: this.subscriptionStatus,
        suscriptionType: this.suscriptionType,
        perPage: this.perPage,
        pageNum: 1
      });
    },
    nextPageCall(pageNum) {
      this.pageNum = pageNum;
      this.listSubscriptions({
        subscriptionStatus: this.subscriptionStatus,
        suscriptionType: this.suscriptionType,
        perPage: this.perPage,
        pageNum: pageNum
      });
    },
    csvExport(arrData) {
      $("#myTable").table2csv();
    }
  }
};
</script>
